import path from 'path';
import fs from 'fs-extra';
import { Plugin } from '../plugin';
import { ServerContext } from '../server/index';
import { CLIENT_PUBLIC_PATH, HMR_PORT } from '../constants';

export function clientInjectPlugin(): Plugin {
  let serverContext: ServerContext;
  return {
    name: 'm-vite:client-inject',
    configureServer(s) {
      serverContext = s;
    },
    resolveId(id) {
      if (id === CLIENT_PUBLIC_PATH) {
        return { id };
      }
      return null;
    },
    async load(id) {
      // Âä†ËΩΩ HMR ÂÆ¢Êà∑Á´ØËÑöÊú¨
      if (id === CLIENT_PUBLIC_PATH) {
        const realPath = path.join(
          serverContext.root,
          'node_modules',
          'mini-vite',
          'dist',
          'client.mjs'
        );
        const code = await fs.readFile(realPath, 'utf-8');
        return {
          // ÊõøÊç¢Âç†‰ΩçÁ¨¶
          code: code.replace('__HMR_PORT__', JSON.stringify(HMR_PORT))
        };
      }
    },
    transformIndexHtml(raw) {
      // ÊèíÂÖ•ÂÆ¢Êà∑Á´ØËÑöÊú¨
      // Âç≥Âú® head Ê†áÁ≠æÂêéÈù¢Âä†‰∏ä <script type="module" src="/@vite/client"></script>
      // Ê≥®: Âú® indexHtml ‰∏≠Èó¥‰ª∂ÈáåÈù¢‰ºöËá™Âä®ÊâßË°å transformIndexHtml Èí©Â≠ê
      return raw.replace(
        /(<head[^>]*>)/i,
        `$1<script type="module" src="${CLIENT_PUBLIC_PATH}"></script>`
      );
    }
  };
}
